(function () {
  'use strict';

  angular.module('k9.pets.edit', [
    'ui.router',
    'k9.models.pets'
  ])

  .controller('EditPetController', function ($window,PhotosModel,appConfig,Upload,BreedsModel,$state, $stateParams, ClientsModel, PetsModel,$mdDialog) {

      var vm                =   this;
      vm.breeds             =   [];
      vm.clickIcon          =   'menu'; //Menu Icon Animation
      vm.editedPet          =   PetsModel.getCurrentPet();
      vm.cancelEditing      =   cancelEditing;
      vm.updatePet          =   updatePet;
      vm.selectedPhoto      =   null;
      vm.selectPhoto        =   selectPhoto;
      vm.goBack             =   goBack;

      // Uploading
      vm.uploadingImage     =   false;
      vm.uploadPetImage     =   uploadPetImage;
      vm.removePetImage     =   removePetImage;
      vm.toggleUploadForm   =   toggleUploadForm;
      vm.isUploading        =   false;
      vm.uploadButtonText   =   '';
      vm.updatePhotoDetails = updatePhotoDetails;

      //Onload
      getBreeds();
      toggleUploadingState(false);

      // setTimeout(function(){
      //    vm.clickIcon = 'keyboard_backspace';
      // }, 1); //removed as on some devices takes too long to animate
      vm.clickIcon = 'keyboard_backspace';

      PetsModel.getPetById($stateParams.petID)
                  .then(function (pet) {
                      if (pet) {
                          PetsModel.setCurrentPet(pet);
                      } else {
                          returnToPets(true);
                      }
                  });

      // Private

      function goBack(){
         $window.history.back();
      }

      function updatePhotoDetails(photo){
        PhotosModel.save(photo)
                   .then(function updatePhotoDetailsSuccess(response) {
                       vm.selectedPhoto=null;
                    });
      }

      function selectPhoto(photo){
        vm.selectedPhoto = photo;
      }

      function toggleUploadingState(on){
        if(on){
          vm.isUploading = true;
          vm.uploadButtonText = 'Uploading. Please wait...';
        }else{
          vm.isUploading = false;
          vm.uploadButtonText = 'Upload Photo';
        }
      }

      function toggleUploadForm(show){
        vm.uploadingImage = show;
      }

      function removePetImage(photo){
        PhotosModel.delete(photo)
                   .then(function removePetImageSuccess(response) {
                      //Remove from in memory array
                      _.remove(vm.editedPet.photos, function(c) {
                        return c.id == photo.id;
                      });
                   });
      }

      function uploadPetImage(photo){
        toggleUploadingState(true);
        Upload.upload({
            url: appConfig.API.baseURL + 'photos',
            method: 'POST',
            data: {
                    'photo[source]'   : photo.file,
                    'photo[title]'    : photo.title,
                    'photo[taken_at]' : photo.dateTaken,
                    'dog_id'          : vm.editedPet.id
                  }
        }).then(function (resp) {
            // Set newly updated image to model
            vm.newphoto = null;
            vm.editedPet.photos.push(resp.data.photo);
            toggleUploadForm(false);
            toggleUploadingState(false);
        }, function (resp) {
            console.log('Error uploading pet image. Status: ' + resp.status);
            toggleUploadingState(false);
        }, function (evt) {
            var progressPercentage = parseInt(100.0 * evt.loaded / evt.total);
            console.log('Upload Progress: ' + progressPercentage + '% ');
       });

      }

      function getBreeds(){
        BreedsModel.getBreeds()
                   .then(function (breeds) {
                      vm.breeds = breeds;
                   });
      }

      function returnToPets(reload) {
        PetsModel.resetCurrentPet();
        $state.go('k9.pets', {}, { reload: false });
      }

      function updatePet() {
        vm.pet = angular.copy(vm.editedPet);
        PetsModel.updatePet(vm.editedPet)
                 .then(function (pets) {
                   PetsModel.resetCurrentPet();
                   goBack();
                 });
      }

      vm.deletePet = function deletePet(pet){

        var confirm = $mdDialog.confirm()
                               .title('Are you sure you would like to delete this dog?')
                               .textContent('Deleting the dog will remove all associated records.')
                               .ok('Cancel')
                               .cancel('Delete Pet');

        $mdDialog.show(confirm).then(function(){
          // Default Confirm is to cancel delete
        }, function() {
          // Delete Pet
          PetsModel.deletePet(pet)
                      .then(function () {
                        ClientsModel.removeDogLocally(pet);
                        goBack();
                      });
        });

      };

      function cancelEditing() {
          returnToPets(false);
      }

  }) // end edit pet ctrl

  .config(function ($stateProvider,$httpProvider,$urlRouterProvider) {

    //State Providers
    $stateProvider.state('k9.pets.edit',{
      url: '/pet/:petID/edit',
      views: {
                'main@' : {
                            controller: 'EditPetController as vm',
                            templateUrl: 'app/pets/edit/pet-edit.tmpl.html'
                          }
             }
    });

  });

}()); // end use strict

(function () {
  'use strict';

  angular.module('k9.pets.create', [
    'ui.router',
    'k9.models.pets'
  ])

  .controller('CreatePetController', function(BreedsModel,$state, $stateParams,
                                              PetsModel) {

    // Public
    var vm        = this;
    vm.isSaving   = isSaving;
    vm.cancel     = cancelCreating;
    vm.createPet  = createPet;
    vm.breeds     = [];

    // On Load
    resetForm();
    getBreeds();

    //Private
    var saving = false;

    function isSaving(){
      return saving;
    }

    function getBreeds(){
      BreedsModel.getBreeds()
                 .then(function (breeds) {
                    vm.breeds = breeds;
                 });
    }

    function returnTopets(reload){
      $state.go('k9.pets', {}, { reload: reload });
    }

    function cancelCreating(){
      returnToPets(false);
    }

    function createPet(pet){
      saving = true;
      PetsModel.createPet(pet)
                  .then(function (pets) {
                    PetsModel.addPet(pets.data.dog);
                    saving = false;
                    $state.go('k9.pets', {petID: pets.data.dog.id}, { reload: true });
                  });
    }

    function resetForm(){
      vm.newPet = {
        name: ''
      };
    }

  }) // end create pet ctrl

  .config(function ($stateProvider) {

    //State Providers
    $stateProvider.state('k9.pets.create',{
      url: '/pet/create',
      views: {
                'main@' : {
                            controller: 'CreatePetController as vm',
                            templateUrl: 'app/pets/create/pet-create.tmpl.html'
                          }
             }
    });

  }); // end ui-router

}()); // end use strict

(function () {
  'use strict';

  angular.module('k9.appointment.create', [
    'ui.router'
  ])

  .controller('CreateAppointmentController', function(
      appConfig, $http, MessagesModel, ActivitiesModel,PetsModel, $log, $state,
      $stateParams,CalendarEventsModel, $mdDialog, selectedDate) {

    //Public
    var vm = this;

    vm.isDisabled               =   false;
    vm.pets                     =   loadAll();
    vm.querySearch              =   querySearch;
    vm.newPet                   =   newPet;
    vm.activities               =   [];
    vm.activityChanged          =   activityChanged;
    vm.isSaving                 =   isSaving;

    // Create Array of Booking Times
    vm.bookingTimes             =   CalendarEventsModel.getBookingTimes();
    vm.createAppointment        =   createAppointment;
    vm.deleteAppointment        =   deleteAppointment;
    vm.createAppointmentCancel  =   createAppointmentCancel;
    vm.newAppointment           =   getNewAppointmentDefaults();
    vm.printReceipt             =   printReceipt;
    vm.sendSMSReminder          =   sendSMSReminder;
    vm.isSMSSent                =   isSMSSent;
    vm.smsSendingDisabled       =   smsSendingDisabled;
    vm.getSMSButtonText         =   getSMSButtonText;

    //OnLoad
    getActivities();

    //Private
    var saving = false;
    var SMSSent = false;

    function getSMSButtonText(){
      var label = 'Send SMS Reminder';
      if(SMSSent){
        label = 'Reminder Sent';
      }else if (saving){
        label = 'Sending. Please wait...';
      }
      return label;
    }

    function smsSendingDisabled(){
      return (saving || SMSSent);
    }

    function isSaving(){
      return saving;
    }

    function isSMSSent(){
      return SMSSent;
    }

    function printReceipt(appointment){

      saving = true;

      //Report URL
      // /api/v1/appointments/:appointment_id/receipt
      var reportURL = appConfig.API.baseURL +
                      'appointments/' +
                      appointment.id +
                      '/receipt' +'.pdf';

      // Get file as blob and display
      // Based on http://stackoverflow.com/a/25808659
      // Cannot just open in new tab as need to pass
      // authentication headers and this is not possible by opening new tab
      $http.get(reportURL, {
          responseType: 'arraybuffer'
        })
        .success(function(data) {
          var file = new Blob([data], {
            type: 'application/pdf'
          });
          var fileURL = URL.createObjectURL(file);
          window.open(fileURL);
          saving = false;  //revert state
        });
    }

    function sendSMSReminder(appointment_id){
      saving = true;
      MessagesModel.sendReminderSMS(appointment_id)
                   .then(function(activity) {
                      saving = false;
                      SMSSent = true;
                    });
    }

    function activityChanged(activityID){
      var activityPromise = ActivitiesModel.getActivityById(activityID);

      activityPromise.then(function(activity) {
        vm.newAppointment.charge = parseFloat(activity.default_charge);
      });

    }

    function getActivities(){
      ActivitiesModel.getActivities()
               .then(function (activities) {
                  vm.activities = activities;
               });
    }

    function newPet(pet) {
      alert("Sorry! More information needed for " + pet.name + " first!");
    }

    function querySearch (query) {
      var results = query ? vm.pets.filter( createFilterFor(query) ) : vm.pets;
      return results;
    }

    // Build `pets` list of key/value pairs
    function loadAll() {
      return PetsModel.getPets().then(function(response) {
          vm.pets = response;
          return response;
      });
    }

    // Create filter function for a query string
    function createFilterFor(query) {
      var lowercaseQuery = angular.lowercase(query);
      return function filterFn(pet) {
        return (pet.name.toLowerCase().indexOf(lowercaseQuery) === 0);
      };
    }

    //Private
    function getNewAppointmentDefaults(){
      var dog = (selectedDate.dog===null || !selectedDate.dog.id) ? '' : selectedDate.dog;
      return {
        id: selectedDate.id,
        title: selectedDate.title || '',
        start: selectedDate.start.toDate(),
        dog:  dog,
        charge: selectedDate.charge,
        activity:  selectedDate.activity || {},
        startTime:  {
                      id    : selectedDate.start.format("HH:00:00"),
                      name  : selectedDate.start.format("HH:00")
                   }
      };
    }

    function resetForm(){
      vm.newAppointment = {
        title: ''
      };
    }

    function createAppointmentCancel(){
      $mdDialog.cancel();
    }

    function createAppointment(appointment){

      console.log(createAppointment,'new appointmetn obj');

      // Set Saving state to true
      saving = true;

      if(appointment.id && appointment.id > 0){
        CalendarEventsModel.updateAppointment(appointment)
                           .then(function (response) {
                              $mdDialog.hide(appointment);
                              saving = false;
                           }, function errorCallback(response) {
                                saving = false;
                                alert('Unable to save at this time...');
                            });
      }else{
        CalendarEventsModel.createAppointment(appointment)
                           .then(function (response) {
                              $mdDialog.hide(appointment);
                              saving = false;
                           }, function errorCallback(response) {
                              saving = false;
                              alert('Unable to save at this time...');
                          });
      }

    }

    function deleteAppointment(appointment){
      CalendarEventsModel.deleteAppointment(appointment)
                         .then(function (response) {
                            $mdDialog.hide(appointment);
                         });
    }

  }) // end create appointment controller

  //Routes
  .config(function ($stateProvider) {

    //State Providers
    $stateProvider.state('k9.dashboard.create',{
      url: '/create/appointment',
      views: {
                'main@' : {
                            controller: 'CreateAppointmentController as vm',
                            templateUrl: 'app/dashboard/create/appointment-create.tmpl.html'
                          }
             }
    });

  });

}()); // end use strict

