(function () {
   'use strict';

    angular.module("k9.models.pets",[])

    .service('PetsModel', function(appConfig, $http, $q){

      //Public
      var vm                =   this;
      vm.getCurrentPet      =   getCurrentPet;
      vm.setCurrentPet      =   setCurrentPet;
      vm.resetCurrentPet    =   resetCurrentPet;
      vm.getPets            =   getPets;
      vm.addPet             =   addPet;
      vm.createPet          =   createPet;
      vm.createPetWithOwner =   createPetWithOwner;
      vm.getPetById         =   getPetById;
      vm.updatePet          =   updatePet;
      vm.deletePet          =   deletePet;

      //Private
      var URLS = { INDEX : appConfig.API.baseURL + 'dogs'},
          pets;

      var currentPet = {
        name: '',
        note:'',
        breed: {
          id:'',
          name:''
        },
        photos:[]
      };

      function getCurrentPet(pet){
        return currentPet;
      }

      function setCurrentPet(pet){
        currentPet.id = pet.id;
        currentPet.name = pet.name;
        currentPet.note = pet.note;
        currentPet.breed = {
          id: (pet.breed !== null) ? pet.breed.id : 0,
          name: (pet.breed !== null) ? pet.breed.name : ''
        };
        currentPet.photos = pet.photos;
      }

      function resetCurrentPet(){
        currentPet.name   = '';
        currentPet.note   = '';
        currentPet.photos = [];
        currentPet.breed  = {
                              id:'',
                              name:''
                            };
      }

      function getPets(){
        return (pets) ? $q.when(pets) : $http.get(URLS.INDEX).then(cachePets);
      }

      function addPet(pet){
        if(pets){
            pets.unshift(pet);
        }
      }

      function createPet(pet){

        //Check for breed
        if(pet.breed){
            pet.breed_id = pet.breed.id; //Makes API call easier
        }

        return $http({
          method  : 'POST',
          url     : appConfig.API.baseURL +'dogs',
          data    : {
                      dog: pet
                    }
        });

      }

      function createPetWithOwner(pet,ownerID){
        // Function creates a new dog that is linked
        // to a particular owner

        //Check for breed
        if(pet.breed){
            pet.breed_id = pet.breed.id; //Makes API call easier
        }

        return $http({
          method  : 'POST',
          url     : appConfig.API.baseURL + 'clients/' + ownerID + '/dogs',
          data    : {
                      dog: pet
                    }
        });

      }

      function getPetById(petID){
        var deferred = $q.defer();

        if (pets) {
            deferred.resolve(findPet(petID));
        } else {
            vm.getPets().then(function () {
                deferred.resolve(findPet(petID));
            });
        }
        return deferred.promise;
      }

      function updatePet(pet){

        //Check for breed
        if(pet.breed){
            pet.breed_id = pet.breed.id; //Makes API call easier
        }

        return $http({
                        method  : 'PATCH', //patch over put for update - https://goo.gl/JRPN2o
                        url     : appConfig.API.baseURL + 'dogs/' + pet.id,
                        data    : {
                                    dog: pet
                                  }

                      }).then(function (response) {
                        // Update pet in local array
                        var pet = response.data.dog;

                        var matchedPet = _.find(pets, function (c) {
                            return c.id == parseInt(pet.id, 10);
                        });

                        matchedPet.name = pet.name;
                        matchedPet.note = pet.note;
                        matchedPet.photos = pet.photos;

                        // Breed
                        if(pet.breed !== null){
                          matchedPet.breed = {
                            id: pet.breed.id,
                            name: pet.breed.name
                          };
                        }else{
                          matchedPet.breed = null;
                        }

                      });
      }

      function deletePet(pet){

        return $http({
          method  : 'DELETE',
          url     :  appConfig.API.baseURL + 'dogs/' + pet.id
        })
        .then(function deletePetsuccessCallback(response) {
          //Remove from in memory array
          _.remove(pets, function(c) {
            return c.id == pet.id;
          });
        });

      }

      function extract(result){
        return result.data.dogs;
      }

      function cachePets(result){
        pets = extract(result);
        return pets;
      }

      function findPet(petID) {
        var result = _.find(pets, function (c) {
            return c.id == parseInt(petID, 10);
        });
        return result;
      }

    }); //end service pet model

}()); //end use strict

(function () {
   'use strict';

   angular.module("k9.models.nav",[
     'ui.router'
   ])

  .service("NavModel", function NavModelService($timeout, $mdSidenav, $log, $compile, $state){

    //Public
    var vm = this;
    vm.menuItem             =   getMenuItem;
    vm.menuIsSelected       =   menuIsSelected;
    vm.getMenuItems         =   getMenuItems;
    vm.getCurrentItem       =   getCurrentItem;
    vm.setCurrentItem       =   setCurrentItem;
    vm.buildDelayedToggler  =   buildDelayedToggler;
    vm.closeSideMenu        =   closeSideMenu;

    //Private

    function closeSideMenu(){
      $mdSidenav('left').close();
    }

    function debounce(func, wait, context) {
      return function debounced() {
        $mdSidenav('left').toggle();
      };
    }

    function buildDelayedToggler(navID) {
      return debounce(function() {
        $mdSidenav(navID)
          .toggle()
          .then(function () {
            $log.debug("toggle " + navID + " is done");
          });
      }, 200);
    }

    var currentItem;
    var menuItem = function menuItem(title,sref,icon){
      // must be a nicer way to do this, feels wrong but not found it yet
      // only purpose is to make creating new object shorter later
      return {
        title : title,
        sref : sref,
        icon : icon
      };
    };

    //Default values
    var menuItems = [
      menuItem('Dashboard','k9.dashboard','dashboard'),
      menuItem('Clients','k9.clients','face'),
      menuItem('Pets','k9.pets','paw'),
      menuItem('Reports','k9.report','my_library_books'), //assessment
      menuItem('Settings','k9.settings.breeds','settings'),
      menuItem('Profile','k9.profile','assignment_ind')
      // menuItem('Social Media','k9.social','facebook-box')
    ];

    function getMenuItem(title,sref){
      return menuItem;
    }

    function menuIsSelected(menuItem){
      if(!currentItem) return false; // in case there is no current item
      return (menuItem.title === currentItem.title) ? true : false;
    }

    function getMenuItems(){
      return menuItems;
    }

    function getCurrentItem(){
      return currentItem;
    }

    function setCurrentItem(menuItem){
      currentItem = menuItem;
    }

  }); //end navigation model

}()); //end use strict

